import { atom } from "recoil";

export const authAtom = atom({
  key: 'authAtom',
  default: {
    isLoggedIn: Boolean(localStorage.getItem('auth')),
    token: null
  },
})

export const userAtom = atom({
  key: 'userAtom',
  default: null
})

export const navigationAtom = atom({
  key: 'navigationAtom',
  default: [
    { name: '', href: '/', current: true },
    { name: '', href: '/vehicle-list', current: false },
    { name: '', href: '/registration', current: false },
  ],
})